<template>
  <div>
    <div class="login_warper">
      <div class="login_img">

      </div>
      <div class="login_form">
        <div class="input_warp" >
          <el-input v-model="userName" placeholder="请输入用户名"></el-input>
        </div>
        <div class="input_warp">
          <el-input v-model="pwd" placeholder="请输入密码"></el-input>
        </div>

        <div class="input_warp" >
          <el-checkbox v-model="rememberUser">记住用户名</el-checkbox>
        </div>
        <div class="btn_warp">
          <span class="btnlogin" @click.stop="login">登录</span>
        </div>
      </div>

    </div>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        userName:'',
        pwd:'',
        rememberUser:false,
      }
    },
    methods: {
      login() {
        let self = this;
        if (!self.validate()) {
          return false;
        }
      },
      validate() {
        let rules = {
          rootCompanyId: { elname: 'company', reg: /[0-9]+/, emptymsg: '请输入公司编码', errormsg: '公司编码不合法' },
          loginName: { elname: 'user', reg: /\S+/, emptymsg: '请输入用户名', errormsg: '用户名错误' },
          password: { elname: 'pwd', reg: /\S+/, emptymsg: '请输入密码', errormsg: '密码错误' }
        }
        let self = this;
        let ob = self.loginData;
        let pass = true;
        for (const key in ob) {
          if (ob.hasOwnProperty(key)) {
            const element = ob[key];
            let rule = rules[key];
            if (rule) {
              if (!element) {
                self.handleStyle(rule.elname, 'error');
                mui.toast(rule.emptymsg);
                pass = false;
                break;
              }
              else if ((typeof element === "string")) {
                if (!rule.reg.test(element)) {
                  self.handleStyle(rule.elname, 'error');
                  mui.toast(rule.errormsg);
                  pass = false;
                  break;
                }
              }
            }
          }
        }
        return pass;
      },
      remember(){
        let _this=this;
        if(_this.rememberUser){
          localStorage.setItem('userName',_this.userName);
        }else {
          localStorage.setItem('userName','');
        }
      }
    },
    created(){
        let _this=this;
        let userName=localStorage.getItem('userName');
        if (!userName){
          _this.userName=userName;
        }
    }
  }
</script>


<style lang="less" scoped>
  @import "../assets/css/login.less";

</style>



